      ],
      answersSection7:[
        ["a)",4,false],
        ["b)",5,false],
        ["c)",6,false]
      ],
      answersSection8:[
        ["a)","El ADN",false],
        ["b)","El DNI",false],
        ["c)","La ONU",false]
      ],
      additional: [
        [],[],[]
      ],
      photos: [
        {
          src: "",
          file: "",
        },
        {
          src: "",
          file: "",
        },
        {
          src: "",
          file: "",
        },
      ],
      info:[
        ["Parabrisas","41"],
        ["Montacargas","42"],
        ["Fotógrafo","43"]
      ],
      // TODO: mostrar borde rojo al usuario si no llena los inputs
      answers: ["", "", "", ""],
      photoPlaceholder: require("@/assets/images/activities-assets/activity_1-1/Recurso 54@4x.png"),
      photoPlaceholder2: require("@/assets/images/activities-assets/activity_1-1/Recurso 51@4x.png"),
      photoPlaceholder3: require("@/assets/images/activities-assets/activity_1-1/Recurso 37@4x.png")
    };
  },
  methods: {
    check_one(i){
      this.additional[i] = [];
    },
    setOptions (e,i) {
      const value = e.target.value;
      let a =""
      if(i==6){
        a = this.answersSection6
      }else if(i==7){
        a = this.answersSection7
      }else{
        a = this.answersSection8
      }
      const rpta = a.map((k)=>{
        if(k[1]==value){
          k[2]=true
        }else{
          if(k[2] == true){
            k[2] = false
          }
        }
        return k
      })
      if(i==6){
        this.answersSection6 = rpta
      }else if(i==7){
        this.answersSection7 = rpta
      }else{
        this.answersSection8 = rpta
      }
    },
    onFileChange(e, id) {
      const file = e.target.files[0];
      const reader = new FileReader();
      this.photos[id].file = file;
      reader.onload = (e) => {
        this.photos[id].src = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    getimg (i) {
      return require(`@/assets/images/activities-assets/activity_1-1/Recurso ${i}@4x.png`)
    }
    // TODO: submit here
  },
  computed: {
  }
};
</script>

